import React, {useEffect, useState} from 'react';
import bridge from '@vkontakte/vk-bridge';
import {Avatar, Button, Div, Panel, PanelHeader, PanelHeaderBack, PanelSpinner, View} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import './panels/game.css'
import Game from './tic-tac-toe/Game'

// const ws = new WebSocket('ws://localhost:5000');
const ws = new WebSocket('wss://vk-app-server.herokuapp.com/');

let id;
let first;
let last;
let avatar;

bridge.send('VKWebAppGetUserInfo')
        .then(data => {
		id = data['id'];
		first = data['first_name'];
		last = data['last_name'];
		avatar = data['photo_200']
		ws.send(`INF${id}&${first}&${last}&${avatar}`);
        })


// const user_id = Math.floor(Math.random() * 1000);


function App() {
	const [panel, setPanel] = useState({current: 'main', previous: ''});
	const [enemy, setEnemy] = useState('Поиск противника...');
	const [game_id, setGame_id] = useState();
	const [turn, setTurn] = useState();
	const [cell, setCell] = useState(0);
	const [loading, setLoading] = useState(true);
	const [data_loading, setData_loading] = useState(true);
	const [user_id, setUser_id] = useState();
	const [first_name, setFirst_name] = useState();
	const [avatar, setAvatar] = useState();
	const [games, setGames] = useState(0);
	const [points, setPoints] = useState(0);
	const [wins, setWins] = useState();

	useEffect(() => {
	    ws.onmessage = response => {
			if (response.data.slice(0, 3) === 'GTF') {
				let data = JSON.parse(response.data.slice(3));
				setEnemy(+data[1]);
				setGame_id(+data[0]);
				setTurn(+data[2]);
				setLoading(false);
			} else if (response.data.slice(0, 3) === 'GTG') {
	    		setCell(response.data.slice(3));
			} else if (response.data.slice(0, 3) === 'GTE') {
				end();
			} else if (response.data.slice(0, 3) === 'INF') {
				let data = JSON.parse(response.data.slice(3));
				setGames(data.games);
				setPoints(data.points);
				setWins(data.wins);
				setData_loading(false);
				setUser_id(id);
				setFirst_name(first);
				setAvatar(avatar);
			}
        };
    });

	function end () {
		setPanel({current: 'main', previous: ''});
		setEnemy('Поиск противника...');
		setGame_id();
		setTurn();
		setLoading(true);
	}

	if (data_loading) {
		return <View activePanel={loading}><Panel id='loading' style={{ textAlign: 'center', marginTop: '100px' }}><h1>Loading...</h1></Panel></View>
	} else {	
		return (
			<View activePanel={panel.current}>
				<Panel id='main'>
					<PanelHeader>Выберите игру, {first_name}</PanelHeader>
					<Div style={{ textAlign: 'center' }}>
						<Avatar src={avatar} size={80} getRootRef={div => div.style='display: inline-block '} />
						<h1>Игр: {games} Побед: {wins} Очков: {points}</h1>
					</Div>
					<Div>
					{/*Кол-во сыгранных игр, побед, */}
						<Button size='xl' stretched style={{ marginTop: 25, height: 60 }}
								onClick={() => setPanel({current: 'tic-tac-toe-hub', previous: panel.current})}>
							Крестики-нолики
						</Button>
						<Button size='xl' stretched style={{ marginTop: 25, height: 60 }} mode='secondary'
								onClick={() => setPanel({current: 'sea-battle-hub', previous: panel.current})}>
							Морской бой
						</Button>
					</Div>
				</Panel>
				<Panel id='tic-tac-toe-hub'>
					<PanelHeader left={<PanelHeaderBack
						onClick={() => setPanel({current: panel.previous, previous: panel.current})}/>}>
						Крестики-нолики
					</PanelHeader>
					<Div>
						<Button size='xl' stretched style={{ marginTop: 25 }}
								onClick={() => {setPanel({current: 'tic-tac-toe', previous: panel.current});
							ws.send(`GTF${user_id}&`)}}>
							Поиск противника
						</Button>
						<Button size='xl' stretched style={{ marginTop: 25 }}
								onClick={() => console.log("Development")}>
							Рейтинг
						</Button>
					</Div>
				</Panel>
				<Panel id='sea-battle-hub'>
					<PanelHeader left={<PanelHeaderBack
						onClick={() => setPanel({current: panel.previous, previous: panel.current})}/>}>
						Морской бой
					</PanelHeader>
					<Div>
						<Button size='xl' stretched style={{ marginTop: 25 }}
								onClick={() => console.log("Development")}>
							Поиск противника
						</Button>
						<Button size='xl' stretched style={{ marginTop: 25 }}
								onClick={() => console.log("Development")}>
							Рейтинг
						</Button>
					</Div>
				</Panel>
				<Panel id='tic-tac-toe' style={{ textAlign: 'center' }}>
					<PanelHeader>
						Битва ID игры: {game_id}
					</PanelHeader>
				<h1>{typeof enemy !== 'string' && 'ID вашего противника:'} {enemy}</h1>
					{!loading ? <Game enemy_id={enemy} game_id={game_id} turn={turn} end={end}
										chip={turn ? 'O' : 'X'} setPanel={setPanel} ws={ws} change={cell}/>
							: <PanelSpinner size='large' style={{ marginTop: 50 }} />
					}
				</Panel>
			</View>
		);
	}
}

export default App;